.footer {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	background-size: cover;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

/*  **********************************  */

.chevron > svg {
	font-size: 5em;
	-webkit-transition: all 0.5s ease;
	-o-transition: all 0.5s ease;
	transition: all 0.5s ease;
}

.chevron > svg:hover {
	cursor: pointer;
	font-size: 7em;
	-webkit-transition: all 0.5s ease;
	-o-transition: all 0.5s ease;
	transition: all 0.5s ease;
}

.chevron {
	height: 7em;
}

/*  **********************************  */

.footer-content {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

/*  **********************************  */

.skills {
	padding: 2em;
	min-width: 350px;
	width: 25%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	background: rgba(255, 255, 255, 0.7);
}

.skills > div {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

.skills li {
	font-size: 1.5em;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}

.skills img, i {
	margin-right: .5em;
	height: 0.9em;
}

/*  **********************************  */

.contact {
	padding: 1em;
	min-width: 350px;
	width: 25%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	background: rgba(255, 255, 255, 0.7);
}

.contact input,
.contact textarea {
	padding: 5px;
	width: 90%;
	margin-bottom: 5px;
	resize: none;
}

.contact form input[type='text'], .contact form input[type='email'], .contact input[type='submit'] {
	height: 30px;
}

.contact input[type='submit'] {
	background: black;
	color: white;
	border: none;
}

.contact input[type='submit']:hover {
	cursor: pointer;
}

.copyright {
	background: rgba(255, 255, 255, 0.4);
	color: black;
	width: 100%;
	text-align: center;
	margin-top: 3em;
}

@media only screen and (max-width: 1100px) {

}

@media only screen and (max-width: 750px) {

	.contact {
		min-width: 100%;
	}
	.footer-content {
		-ms-flex-direction: column;
		    flex-direction: column;
	}

	.skills, .contact {
		width: 100%;
	}

	.skills {
		margin-bottom: 3em;
	}

}



