.sidemenu.col-to-row {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	width: 33%;
	-ms-flex-pack: center;
	    justify-content: center;
}

.sidemenu > .col {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
}

.profile-pic {
	border-radius: 20px;
	width: 150px;
	height: 150px;
	margin: 5px 0 5px 0;
}

.sidemenu header {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	text-align: center;
}

.sidemenu .links {
	margin: 10px;
}

.sidemenu .intro {
	width: 85%;
	white-space: pre-wrap;
	text-align: center;
}

@media only screen and (max-width: 1100px) {
	.sidemenu.col-to-row {
		width: 100%;
		-ms-flex-direction: row;
		    flex-direction: row;
		-ms-flex-pack: center;
		    justify-content: center;
	}

	.sidemenu > .col {
		width: 40%;
		padding: 10px;
	}

	.sidemenu .intro {
		width: 100%;
		white-space: pre-wrap;
	}
}

@media only screen and (max-width: 600px) {
	.sidemenu.col-to-row {
		-ms-flex-direction: column;
		    flex-direction: column;
	}

	.sidemenu > .col {
		width: 100%;
		padding: 10px 50px;
	}
}



