.App {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	font-family: 'Crimson Text', serif;
}

.App > .row-to-col {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	padding: 20px;
	height: 75vh;
	position: relative;
}

.App > footer {
	height: 80vh;
}

@media only screen and (max-width: 1100px) {
	html,
	body, .App{
		overflow-x: hidden;
		height: auto;
	}

	.App > .row-to-col {
		-ms-flex-direction: column;
		    flex-direction: column;
		padding: 10px;
		height: auto;
		-ms-flex-align: center;
		    align-items: center;
	}

	.App > footer {
		height: auto;
	}
}

@media only screen and (max-height: 800px) and (min-width: 1100px){

	.App > .row-to-col {
		height: 600px;
	}

	.App > footer {
		height: 640px;
	}
}



